<template>
  <div class="mypage-top">
    <div class="mypage-blank-space"></div>
    <div>
      {{userStat.nnm}} 
    <router-link to="/checkPwd?loc=info"><el-button> </el-button></router-link>
    <!-- <router-link to="/checkPwd"><el-button> </el-button></router-link> -->
    </div>
    <el-card>
      <h1>   </h1>
      <ul>
        <li v-for="(item, index) in userHistory" :key="index">
          <!-- <el-button @click="toggleOn"><span>2022.07.14 13:21     </span><span id="que-1-toggle"></span></el-button> -->
          <el-button @click="toggleOn(index)"><span>{{item.start_time}} {{item.title}} {{item._win ? "" : ""}}</span><span id="que-1-toggle"> </span></el-button>
          <div v-if="show[index]">  : {{item.position}} <br>  :  {{item.max_num}}  {{item.agree}}  {{item.disagree}}  {{item.invalid}}</div>
        </li>
      </ul>
    </el-card>
    <dialog></dialog>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
 data () {
  return {
    show : [],

  }
 },
created () {
  this.$store.dispatch("getUserHistory")
  this.$store.dispatch("getUserStat")
},
 computed : {
    ...mapState(["userHistory", "userStat"])
  },
 methods : {
  toggleOn(index) {
    this.show[index] = !this.show[index];
  },
 },
 mounted() {
  for (let i = 0; i < this.userHistory.length; i++) {
    this.show.push(false);
  }
 }

}

</script>

<style scoped>

.answer {
    display: none;
    padding-bottom: 30px;
  }
.mypage-blank-space {
      height: 100px;
  }
</style>
